body{color:#fff;background-color:#000;font-family:Avenir, "Century Gothic", Arial, sans-serif;font-size:16px;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}canvas{position:fixed;top:0;left:0}a{text-decoration:none;color:#ea85a1;outline:none}a:hover,a:focus{color:#fff;outline:none}.overlay{position:absolute;z-index:2;top:0;left:0;width:20%;height:20%;display:flex;color:#ffffff;background:rgba(0,0,0,0);-webkit-touch-callout:none;-webkit-user-select:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.bg-white .bg-white::before .bg-white::after{background-color:#ffffff}#ProjectInfo{font-stretch:normal;font-style:normal;line-height:normal;letter-spacing:normal;color:#ffffff}.ProjectTitle{font-size:28px;font-weight:900}.Line{width:100%}.Description{font-size:10px;font-weight:500;font-style:oblique}.InfoLink{font-size:16px;font-weight:900;font-style:oblique;color:#ea85a1}.NavText{font-size:14px;font-weight:900;font-style:oblique}
